.workInfoBlock_wrapper {
  display: flex;
  justify-content: space-between;
  background-color: #f0f2f5;
  margin: 16px;
  -webkit-box-shadow: 0 4px 4px rgb(0 0 0 / 25%);
  box-shadow: 0 4px 10px rgb(0 0 0 / 25%);
}

.workInfoBlock_container {
  width: 80%;
}

.workInfoBlock_logo {
  width: 16%;
  /* border: solid 2px green; */
  align-self: center;
}

.block_name {
  width: 40%;
  font-family: 'Quicksand', sans-serif;
  font-size: 56px;
  font-weight: 500;
  line-height: 74px;
  letter-spacing: -0.03em;
  text-transform: capitalize;
  color: #2c333f;
  margin-top: 8%;
  margin-left: 36px;
}

.workInfoBlock_title {
  font-family: 'Quicksand', sans-serif;
  font-size: 20px;
  font-weight: 700;
  line-height: 32px;
  letter-spacing: -0.03em;
  color: #2c333f;
  margin: 24px 24px 16px;
}

.workInfoBlock_data {
  margin: 0px 24px 16px;
  font-weight: 500;
}

.workInfoBlock_infoBlock {
  font-family: Montserrat, sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 32px;
  color: #2c333f;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  -ms-flex-pack: justify;
  justify-content: space-between;
  flex: 1;
  height: auto;
  margin-bottom: 15px;
  margin-left: 24px;
  overflow: auto;
}

.link_a:link,
.link_a:visited {
  cursor: pointer;
}

.link_a text,
.link_a {
  fill: blue;
  text-decoration: underline;
}

.link_a:hover,
.link_a:active {
  outline: dotted 1px blue;
}
